/*垂直居中*/
.absPosMiddle(){
    top:0;
    bottom: 0;
    position: absolute;
    margin:auto;
}
.absPosHMiddle(){//绝对水平居中
  left:0;
  right: 0;
  position: absolute;
  margin:auto;
}
.absPosCenter(){
    top:0;
    bottom: 0;
    left: 0;
    right: 0;
    position: absolute;
    margin:auto;
}
.opacity (@opacity)
{
  opacity: @opacity;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(opacity=(@opacity * 100))";
  filter: "alpha(opacity = (@opacity * 100))";
}
@mprZoom:1200;
@maskZ:9000;
/*简写*/
.relative{position: relative;}
/*三角形小标 （利用border）    t为三角形的高 o为三角形的底*/
.diyTriangle(left,@t,@o,@tc,@oc){/*     tc: target color ;oc:others color     */
  height:0;
  width: 0;
  border: 0;
  border-bottom:@o solid @oc;
  border-right:@t solid @tc;
  border-top:@o solid @oc;
}
.diyTriangle(right,@t,@o,@tc,@oc){
  height:0;
  width: 0;
  border: 0;
  border-bottom:@o solid @oc;
  border-left:@t solid @tc;
  border-top:@o solid @oc;
}
.diyTriangle(top,@t,@o,@tc,@oc){
  height:0;
  width: 0;
  border: 0;
  border-bottom:@t solid @tc;
  border-left:@o solid @oc;
  border-right:@o solid @oc;
}
.diyTriangle(bottom,@t,@o,@tc,@oc){
  height:0;
  width: 0;
  border: 0;
  border-left:@o solid @oc;
  border-right:@o solid @oc;
  border-top:@t solid @tc;
}
.clear  {
  &:after {
    content: ".";
    display: block;
    height: 0;
    clear: both;
    visibility: hidden
  }
}
/*景图像等比缩放到宽度或高度与容器的宽度或高度相等，背景图像始终包含在容器内*/
.x-img
{
  -webkit-background-size:contain;
  background-size:contain;
  background-repeat:no-repeat;
  background-position:center;
}
