@import (reference) "widgets";

#unitMask{
  z-index: @maskZ;
  position: fixed;
  top: 0;left: 0;width: 100%;height: 100%;
}




#unitLayoutSelector,#unitInnerLayoutSelector{
  position: absolute;box-sizing: content-box;border: 1px solid #5A5A5A;
  padding: 1px;
  background-color: #5A5A5A;z-index: @maskZ + 1000;
  .urow{.clear;
    margin-left:0;width: 100%;
    margin-right:0;
  }
  .ucol{
    float: left;
    width: 20px;
    height: 20px;
    margin: 2px;
    background-color: #828282;
    box-sizing: border-box;border: 1px solid transparent;
  }
  .ucol.lighten{
    border:1px solid white;background-color: #B4B4B4;
  }
}

