#quickWWWLBox,#downloadBox,#commonSet,.magnifyBox,.layoutBox,.filterBox,.setBox,.printSetBox,.wwwlBox,.wwwlSetBox,.printTagSetbox{
  .absPosCenter;
  z-index: @maskZ + 1000;
  width: 360px;
  height: 400px;
  display: none;
  padding:0 5px 10px;background-color: #E9E7E2;font-size: 14px;
  .topbar{
    height: 30px;
    .closeBtn{
      font-size: 14px;
      position: absolute;background-color: #FF7D6F;color: white;top:-2px;right:10px;padding: 2px 5px;
    }
    .closeBtn:hover{
      cursor: hand;
    }
    .topbarTitle{
      color: #888;
      display: block;
      line-height: 30px;
    }
  }
  .calBox{
    background-color: #4D4D4D;
    height: 40px;
    overflow: hidden;
    padding: 8px 10px;
    .inputArea,.yes{
      display: inline-block;
    }
    .inputArea{width: 120px;
      input,select{
        display: inline-block;color:black;
        height: 24px;
        padding-left: 8px;
        background-color: #e9e7e2;
        border: 0;
        width: 60px;
      }
    }
    .yes{
      cursor: pointer;
      height: 24px;
      line-height: 24px;
      display: inline-block;
      text-align: center;
      background-color: #918b87;
      margin-right: 5px;
      width: 45px;
      color: #e9e7e2;
    }
    .yes:hover{
      background-color: darken(#918b87,10%);
    }
  }
  .ceilBox{
    padding: 5px;height: 300px;
    position: relative;
    background-color: #3F3F3F;
    overflow-x: hidden;
    overflow-y: scroll;
    &::-webkit-scrollbar {/*滚动条，这里不倒腾用户体验了，暂时在满足webkit，这里主要是想节约空间，其实大可不必通过滚动条*/
      width:8px;
      height:8px;
    }
    &::-webkit-scrollbar-thumb{
      background:#888888;
      border-radius:4px;
    }
    &::-webkit-scrollbar-track{//外层轨道
      background:#424242;width:8px;
    }
    &::-webkit-scrollbar-button    {
      background-color:#424242;height:4px;
    }
    color:#888888;
    .aCeil{
      background-color: #E9E7E2;padding:6px 10px;cursor: pointer;
      margin: 8px;text-align: center;display: block;float: left;
    }
    .aCeil:hover{
      background-color: darken(#E9E7E2,20%);color:black;
    }
  }
}
//下载选项框
#downloadBox{
  height:210px;
  display: none;
  .calBoxLoad {
    //background-color: #4D4D4D;
    // height: 40px;
    overflow: hidden;
    padding: 8px 10px;
    ul{
      li{
        width: 100%;
        height:40px;
        padding-top: 10px;
        padding-left: 10px;
        margin-bottom: 10px;
        background-color: #4D4D4D;
        display: block;
        &:hover{
        background-color:cadetblue;
        }
      }
    }
  }
}
//打印页面
@rightArea:200px;
@liW:160px;

.processingBox{
  width: 90%;
  margin: 0 auto;.relative;
  .hidebtn{
    font-size: 14px;border-radius: 3px;line-height: 16px;cursor: pointer;
    position: absolute;background-color: #FF7D6F;color: white;top:2px;right:0px;padding: 1px 3px;
  }
  .loadingTxt{font-size: 14px}
  .failed{
    font-size: 14px;
    display: inline-block;
    .tryAgain{
      color:#00aaff;cursor: pointer;
    }
  }
}

#playSpeedSet{
  .absPosCenter;z-index: @maskZ + 2000;
  width: 300px;
  height: 40px;
  background-color: #0b97c4;
  padding-left: 10px;padding-top: 10px;
  .rangeBar,span{display: inline-block;}
  span{
    text-align: center;width: 30px;font-size: 14px;
  }
  .rangeBar{width: 200px;}
}