@import (reference) "widgets";
*{
  padding: 0;margin: 0;
}
html,body{
  height: 100%;
}
body{
  font-size: 16px;
  height:100%;font-family:"Microsoft YaHei","微软雅黑","Helvetica Neue", "Luxi Sans","Microsoft JhengHei","华文细黑",STHeiti,MingLiu;
  color:white;
}
table {
  border-collapse: collapse;
  border-spacing: 0
}
a {
  cursor: pointer;
  text-decoration: none;
}
ul,ol {
  list-style: none;
}