  .viewer {
    margin-left: @toolW;
    .imageViewer {
      margin-top: @headH ;
      //margin-left: @verticalToolW;
      height: 100%;
      width: 100%;
      overflow: hidden;
      box-sizing: border-box;
      position: relative;
      .viewport {
        width: 100%;
        height: 100%;
        .relative;
      }
      .seriesWindow {
        float: left;
        position: relative;
        border: 2px solid #777777;;
        //border: 2px solid #000000;;
        box-sizing: border-box;
        overflow: hidden;
        //播放控制栏
        .playcontrol{
          padding-right: 5px;
          padding-left: 5px;
          background-color: #383838;
          z-index: 1001;
          //width: 150px;
          height: 28px;
          position: absolute;
          left: 10%;
          top: 8px;
          .btnArea {
            margin-top: 2px;
            padding-left: 2px;
            padding-right: 2px;
            //width: 25px;
            width: 20%;
            float: left;
            pointer-events: auto;
            position: relative;
            display: inline-block;
            .btn {
              border-radius: 1px!important;
              cursor: pointer;
              width: 100%;
              height: 100%;
              position: relative;
              .icon {
                .absPosCenter;.x-img;
                width: 14px;
                height: 14px;
              }
            }
          }

        }
        //右上按钮区域
        .expand {
          position: absolute;
          top: 8px;
          right: 8px;
          width: 200px;
          height: 30px;
          z-index: 9999;
          font-size: 10px;


          .btnArea {
            float: right;
            margin-left: 5px;
            text-align: center;
            opacity: 0.7;
            background-color: #000000;//@topAreaColor;
            width: 30px;
            height: 100%;
            z-index: 10000;
            position: relative;
            //margin-top: 5px;
            display: inline-block;
            .btn {
              border-radius: 1px!important;
              &:hover {
                background:  @hoverColor;
              }
              width: 100%;
              height: 100%;
              position: relative;
              .icon {
                .absPosCenter;.x-img;
                width: 24px;
                height: 24px;
              }
            }

            //按钮选中
            .btnActive{
              background-color: @btnActiveColor2;
              opacity: 0.9;
            };
          }
        }
      }
      //图像选中边框设置
      .seriesWindow.checked {
        //border:1px solid #FFFF66;
        border: 2px dashed #00BC8C;
        //border: 2px dashed dodgerblue;
      }
      .viewportWrapper {
        float: left;
        box-sizing: border-box;
        position: relative;
        border: 1px solid #777777;
        //border: 1px solid #000000;
        .overlay {
          position: absolute;
          color: #e4ad00;
          font-size: 12px;
        }
        @media (min-width: 1200px) {
          .overlay {
            font-size: 14px;
          }
        }
      }
    }

  }
