    background-color: white;height: 100%;width: 100%;position: absolute;top:0;left: 0;
    .opacity(0.5);
  }
  .mprClose{
    position: absolute;top:10px;right: 20px;cursor: pointer;
    color: red;
    background-color: #CCCCCC;
    //background-color: #fefefe;
    width: 30px;height: 30px;z-index: @maskZ + 100002;font-size: 30px;line-height: 30px;text-align: center;
  }
  .mprSmall{
    position: absolute;top:10px;right: 60px;cursor: pointer;
